body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
    min-height: 100% !important;
    height: 100%;
    overflow-x: hidden;
}

.dashboard-container{
    margin-top: 2rem!important;
}

.chart-content{
    margin: 0 auto;
}

.chart-container{
    margin-top: 0.5rem;
}
.bills-container{
    padding: 5rem;
    margin: 0 auto;
}
.bills-row:hover{
    background-color: rgba(69, 84, 88, 0.35);
}
.login-form-container {
    margin-top: 6rem!important;
}

.login-form-content{
    display: grid;
    padding: .7rem;
}
.app-bar-brand:hover {
    background-color: #2f3e42;
}

.app-bar-brand {
    padding: 5px;
}

.btn {
    position: relative;
    padding: 0.5rem;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.btn span {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    tranform: scale(1, 1);
}

.btn::before, .btn::after {
    content: '';
    position: absolute;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.btn-one::before {
    left: 4px;
    z-index: 1;

    opacity: 0;
    background: rgba(255, 255, 255, 0.1);
    -webkit-transform: scale(0.1, 1);
            transform: scale(0.1, 1);
}

.btn-one:hover::before {
    opacity: 1;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
}

.btn-one::after {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    border: 1px solid rgba(255, 255, 255, 0.5);
}

.btn-one:hover::after {
    -webkit-transform: scale(1, .1);
            transform: scale(1, .1);
    opacity: 0;
}

.itu-logo{
  height: 30px;
  opacity: 0.5;
  position: fixed;
  bottom: 0;
  right: 0;
}
.itu-logo:hover{
  -webkit-transition: 200ms;
  transition: 200ms;
  opacity: 1;
}



.alarms-container{
    margin: 2rem;
    padding: 1rem;
}

.alarm-row:hover{
    background-color: rgba(69, 84, 88, 0.35);
}
